import Vue from 'vue'
import Vuex from 'vuex'
import api from '@/api'
var mqtt = require('../util/mqtt.min.js'); //  mqtt 
const crypto = require('../util/hex_hmac_sha1.js'); //
Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		store: {},
		cart: [],
		orderType: 'takein',
		member: {},
		order: {},
		client: null,
		mqtt: mqtt,
		isConnect: false
	},
	getters: {
		isLogin: state => Object.keys(state.member).length > 0	//
	},
	mutations: {
		SET_ORDER_TYPE(state, type) {
			state.orderType = type
		},
		SET_MEMBER(state, member) {
			state.member = member
		},
		SET_STORE(state, store) {
			state.store = store
		},
		SET_CART(state, cart) {
			state.cart = cart
		},
		REMOVE_CART(state) {
			state.cart = []
		},
		SET_ORDER(state, order) {
			state.order = order
		},
		SET_ORDERS(state, orders) {
		  //  state  Member.orders
		  state.Member.orders = orders;
		},
		UPDATE_MEMBER(state, memberData) {
		  //  state  Member
		  Object.assign(state.Member, memberData);
		},
		setClient(state, client) {
			state.client = client;
		},
	},
	actions: {
	  async getStore({ commit }) {
	    const store = await api('store');
	    commit('SET_STORE', store);
	  },
	  updateOrders({ commit }, orders) {
	    commit('SET_ORDERS', orders);
	  },
	  updateMember({ commit }, memberData) {
	    commit('UPDATE_MEMBER', memberData);
	  },
	  connectMqtt({ commit, state }) {
		if(state.isConnect)
		{
			return;
		}
	    const deviceConfig = {
	          productKey: "k1n5mcDd3Fr",
	          deviceName: "mini",
	          deviceSecret: "fc91ea19f38ca586285eadcb72a5bd71",
	          regionId: "cn-shanghai"
	        };

			const params = {
			      productKey: deviceConfig.productKey,
			      deviceName: deviceConfig.deviceName,
			      timestamp: Date.now(),
			      clientId: Math.random().toString(36).substr(2),
			    }
			//CONNECT
			const options = {
			  keepalive: 60, //60s
			  clean: true, //cleanSession
			  protocolVersion: 4 //MQTT v3.1.1
			}
			//1.clientIdusernamepassword
			
			
			let keys = Object.keys(params).sort();
			// 
			keys = keys.sort();
			const list = [];
			keys.map((key) => {
			  list.push(`${key}${params[key]}`);
			});
			const contentStr = list.join('');
			options.password = crypto.hex_hmac_sha1(deviceConfig.deviceSecret, contentStr);
			
			options.clientId = `${params.clientId}|securemode=2,signmethod=hmacsha1,timestamp=${params.timestamp}|`;
			options.username = `${params.deviceName}&${params.productKey}`;
			
	        console.log(options)
	        //productKeywxswssws
	        state.client = mqtt.connect('wxs://k1n5mcDd3Fr.iot-as-mqtt.cn-shanghai.aliyuncs.com',options)
	        state.client.on('connect', function () {
			  state.isConnect = true;
	          console.log('')
	          //productKeydeviceName(--Topic )
	          state.client.subscribe('/k1n5mcDd3Fr/mini/user/get', function (err) {
	            if (!err) {
	               console.log('');
	            }
	          })
	        })
	    	//
	        state.client.on('message', function (topic, message) {
	          // message is Buffer
	          let msg = message.toString();
	          console.log(''+msg);
	         // client.end()
			})
	  },
	  
	}

})

export default store